.contact-infos{
    background-color: rgb(34, 42, 51);
    color: rgb(255, 255, 255);
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-list{
    list-style: none;
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 4px;
}

.contact-list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-container{
    padding-left: 36px;
    padding-right: 36px;
}

.mw-235{
    min-width: 235px;
}

.mw-140{
    min-width: 140px;
}

.mw-180{
    min-width: 180px;
}

.mw-285{
    min-width: 285px;
}